import { gql } from '@apollo/client';
import { Mutation, OperationInput } from '@deity/falcon-data';
import { RemoveCartItemInput, RemoveCartItemPayload } from '@deity/falcon-shop-extension';
import { GET_CART } from './CartQuery';

export const REMOVE_CART_ITEM = gql`
  mutation RemoveCartItem($input: RemoveCartItemInput!) {
    removeCartItem(input: $input) {
      id
    }
  }
`;

export type RemoveCartItemResponse = {
  removeCartItem: RemoveCartItemPayload;
};

export class RemoveCartItemMutation extends Mutation<RemoveCartItemResponse, OperationInput<RemoveCartItemInput>> {
  static defaultProps = {
    mutation: REMOVE_CART_ITEM,
    refetchQueries: ['Cart', 'MiniCart', 'CheckoutState'],
    awaitRefetchQueries: true,
    update: (store: any, { data: { removeCartItem } }: any) => {
      const data = store.readQuery({
        query: GET_CART
      });

      const updatedCart = {};

      if (removeCartItem) {
        const { id } = removeCartItem;

        updatedCart['items'] = data.cart.items.filter((item: any) => item.id !== id);
        updatedCart['itemsQty'] = data.cart.items.length;
      } else {
        // Something isn't right.. clear the entire cart.
        // `removeCartItem` can be `null` if the user adds something to their cart and then logs out
        updatedCart['items'] = [];
        updatedCart['itemsQty'] = data.cart.items.length;
      }

      store.writeQuery({
        query: GET_CART,
        data: {
          ...data,
          cart: {
            ...data.cart,
            ...updatedCart
          }
        }
      });
    }
  };
}
