import { gql } from '@apollo/client';
import { Query } from '@deity/falcon-data';

export const IS_EMAIL_TAKEN = gql`
  query isEmailTaken($email: String!) {
    isEmailTaken(email: $email)
  }
`;

export type IsEmailTakenResponse = {
  isEmailTaken: Boolean;
};

export type IsEmailTakenVariables = {
  email: string;
};

export class IsEmailTakenQuery extends Query<IsEmailTakenResponse, IsEmailTakenVariables> {
  static defaultProps = {
    query: IS_EMAIL_TAKEN
  };
}
