import { gql } from '@apollo/client';
import { Query } from '@deity/falcon-data';
import { CheckoutValues } from '@deity/falcon-shop-extension';

export const CHECKOUT_STATE = gql`
  query CheckoutState {
    checkoutState {
      email
      shippingAddress {
        firstname
        lastname
        street
        city
        postcode
        company
        telephone
        id
        country {
          id
          code
          englishName
          localName
        }
        region {
          id
          code
          name
        }
        email
        saveInAddressBook
      }
      billingAddress {
        firstname
        lastname
        street
        city
        postcode
        company
        telephone
        id
        country {
          id
          code
          englishName
          localName
        }
        region {
          id
          code
          name
        }
        email
        saveInAddressBook
      }
      billingSameAsShipping
      shippingMethod {
        carrierTitle
        method
        provider
        title
        amount
        priceExclTax
        priceInclTax
        currency
      }
      paymentMethod {
        provider
        method
        config
        surcharge {
          amount
          fixed
          percentage
        }
      }
    }
  }
`;

export type CheckoutStateResponse = {
  checkoutState: CheckoutValues;
};

export class CheckoutStateQuery extends Query<CheckoutStateResponse> {
  static defaultProps = {
    query: CHECKOUT_STATE,
    // Addresses here may not have an ID, so it shouldn't be stored in the cache (otherwise it may pick the wrong address)
    fetchPolicy: 'no-cache'
  };
}
