import { gql } from '@apollo/client';
import { Cart, CartItem } from '@deity/falcon-shop-extension';
import { Query } from '@deity/falcon-data';

export const GET_MINI_CART = gql`
  query MiniCart {
    cart {
      itemsQty
      quoteCurrency
      items {
        itemId
        id
        sku
        qty
        name
        price
        regularPrice
        rowTotal
        rowTotalWithDiscount
        thumbnailUrl
      }
    }
  }
`;

export type MiniCartResponse = {
  cart: Pick<Cart, 'itemsQty' | 'quoteCurrency'> & {
    items: Pick<
      CartItem,
      'id' | 'sku' | 'qty' | 'name' | 'price' | 'regularPrice' | 'rowTotal' | 'rowTotalWithDiscount' | 'thumbnailUrl'
    >[];
  };
};

export class MiniCartQuery extends Query<MiniCartResponse> {
  static defaultProps = {
    ssr: false,
    query: GET_MINI_CART
  };
}
