import { gql, useMutation, MutationHookOptions } from '@apollo/client';
import { Mutation } from '@deity/falcon-data';

export const RESTORE_CART = gql`
  mutation RestoreCart {
    restoreCart
  }
`;

export type RestoreCartResponse = {
  restoreCart: boolean;
};

export class RestoreCartMutation extends Mutation<RestoreCartResponse> {
  static defaultProps = {
    mutation: RESTORE_CART,
    refetchQueries: ['CheckoutState', 'Cart', 'MiniCart'],
    awaitRefetchQueries: true
  };
}

export const useRestoreCartMutation = (options: MutationHookOptions<RestoreCartResponse>) =>
  useMutation(RESTORE_CART, {
    refetchQueries: ['CheckoutState', 'Cart', 'MiniCart'],
    awaitRefetchQueries: true
  });
