import { gql } from '@apollo/client';
import { Order, OrderItem, Address, Country, ShippingMethod, PaymentMethod } from '@deity/falcon-shop-extension';
import { Query } from '@deity/falcon-data';

export const GET_ORDER = gql`
  query Order($id: ID!) {
    order(id: $id) {
      id
      referenceNo
      createdAt
      customerFirstname
      customerLastname
      status
      currency
      shippingMethod {
        carrierTitle
        provider
        method
        title
        amount
      }
      paymentMethod {
        provider
        method
        surcharge {
          amount
        }
      }
      totals {
        code
        title
        value
      }
      items {
        id
        sku
        name
        rowTotalInclTax
        qty
        thumbnailUrl
        link
      }
      billingAddress {
        company
        firstname
        lastname
        street
        city
        postcode
        country {
          code
        }
        telephone
      }
      shippingAddress {
        company
        firstname
        lastname
        street
        city
        postcode
        country {
          code
        }
        telephone
      }
    }
  }
`;

export type OrderResponse = {
  order: Pick<
    Order,
    'id' | 'referenceNo' | 'createdAt' | 'customerFirstname' | 'customerLastname' | 'status' | 'currency' | 'totals'
  > & {
    items: Pick<OrderItem, 'id' | 'sku' | 'name' | 'rowTotalInclTax' | 'qty' | 'thumbnailUrl' | 'link'>[];
    billingAddress: Pick<
      Address,
      'company' | 'firstname' | 'lastname' | 'street' | 'city' | 'postcode' | 'telephone'
    > & {
      country: Pick<Country, 'code'>;
    };
    shippingAddress: Pick<
      Address,
      'company' | 'firstname' | 'lastname' | 'street' | 'city' | 'postcode' | 'telephone'
    > & {
      country: Pick<Country, 'code'>;
    };
    shippingMethod?: Pick<ShippingMethod, 'carrierTitle' | 'provider' | 'method' | 'title' | 'amount'>;
    paymentMethod: Pick<PaymentMethod, 'provider' | 'method' | 'surcharge'>;
  };
};

export class OrderQuery extends Query<OrderResponse> {
  static defaultProps = {
    query: GET_ORDER
  };
}
