import { gql } from '@apollo/client';
import { Query } from '@deity/falcon-data';
import { PaymentMethod } from '@deity/falcon-shop-extension';

export const PAYMENT_METHOD = gql`
  query PaymentMethod {
    paymentMethod {
      provider
      method
      config
      surcharge {
        amount
        fixed
        percentage
      }
    }
  }
`;

export type PaymentMethodResponse = {
  paymentMethod: PaymentMethod;
};

export class PaymentMethodQuery extends Query<PaymentMethodResponse> {
  static defaultProps = {
    query: PAYMENT_METHOD
  };
}
